.header {
    grid-area: h;
    float: left;

    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    color: #3f4257;
    padding: 0 10px;
}
.appBrand {
    display: flex;
    float: left;
    text-transform: uppercase;
}
.logoImage {
    display: flex;
    margin-top: 10px;;
    width: 70px;
    height: 70px;
}
.loginBlock {
    display: flex;
    float: right;
    margin: 30px;
}
.btnLogin {
    background: #3f4257 ;
}
.btnLogout {
    margin-left: 10px;
}
.header h6 {
    font-size: .8rem;
    padding-top: 30px;
}
.header h5, h6 {
    display: block;
    margin: 0;
    width: 120px;
}
.header h5 {
    font-size: 9px;
    font-weight: bold;
}
