.descriptionBlock {
    display: flex;
}
.profilePhoto {
    padding: 10px;
    display: block;
    width: 300px;
}
.infoBlock {
    display: block;
    margin: 10px;
}
.contBlock {
    width: 1000px;
}
.mainPhoto {
    max-width: 250px;
}
.contact {
    padding-left: 10px;
}
input {
    display: block;
}
input::file-selector-button {
    display: inline-block;
    margin: 10px 10px 10px 0;
    padding: 5px 10px;

    font-family: inherit;
    font-size: 1rem;
    line-height: 1.1;
    color: #fff;
    font-weight: 500;
    text-decoration: none;

    border: 0;
    cursor: pointer;
    background: #ff6200;
    border-radius: 5px;
    transition: background .2s linear;
}
input::file-selector-button:hover {
    background: #285a9f;
}